body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee;
}

#header-text{
  color:#444;
}

#books-container{
  box-sizing: border-box;
  height: 100%;
}
#BookList{
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 2px solid #880E4F;
  box-shadow: 3px 4px 6px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  color: #880E4F;
  font-weight: 500;
  font-size: 20px;
}

form{
   left: 0;
  bottom: 0; 
  background: #fff;
  width: 400px; 
  height: 35vh;
}

 form .form-group{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
 }  

 form label{
  text-align: right;
  padding: 6px;
} 
form select, form input{
  padding: 6px;
  margin: 4px;
  box-sizing: border-box;
}  

form button {
  color: #fff;
  font-size: 2em;
  background: #AD1457;
  border: 0;
  padding: 0 12px;
  border-radius: 50%;
  cursor: pointer;
  left: 25px;
}

#book-Display{
  background: #be1f64;
  top: 0;
  right: 20px;
  padding: 30px;
  box-sizing: border-box;
  color: #fff;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
  overflow: auto;
  height: 100vh;
}

#Book-Details h3{
  font-size: 35px;
  font-size: 600;
}

#Book-Details p, #Book-Details h5,  #Book-Details ul li{
  font-size: 20px;
  font-size: 500;
}
